        <div class="modal-body">
          <div class="textCard">
            <p style="font-size: 12px;">
              1.	协诊的整个进行流程如下：
            </p>
            <p style="font-size: 14px; text-indent: 30px;">
              ①选择协诊专家  ②填写预约信息    ③请求专家协诊     ④完善患者病情信息 ⑤扫码支付诊费
            </p>
            <p style="font-size: 12px;text-indent: 30px;"> ⑥专家按预约时间提供远程协诊   ⑦专家提供诊疗意见 ⑧接收/查看诊疗意见</p>
          </div>
          <div class="textCard">
            <p style="font-size: 14px;">
              2.	预约协诊时请准确填写患者资料及其详细病情资料，以便专家做出准确的诊断；
            </p>
          </div>
          <div class="textCard">
            <p style="font-size: 14px;">
              3.	成功预约协诊后，专家将会在预约时间段内发起协诊，届时请提前做好准备（登录系统并保证网络畅通，摄像头及语音设备正常）；
            </p>
          </div>
          <div class="textCard">
            <p style="font-size: 14px;">
              4.	在预约时间段内如遇突发情况专家未发起协诊，则协诊未生效，所交费用将由平台工作人员返回原账号；
            </p>
          </div>
          <div class="textCard">
            <p style="font-size: 14px;">
              5.	在预约时间段内专家发起协诊，但超时未响应专家邀请则协诊失效，因专家费用已支付将不进行退款。因此请在预约时间段前提前做好协诊准备；
            </p>
          </div>
          <div class="textCard">
            <p style="font-size: 14px;">
              6.	如对专家的协诊服务不满意，请在结束服务后24小时内致电客服沟通；
            </p>
          </div>
          <div class="textCard">
            <p style="font-size: 14px;">
              7.	专家的意见仅作为诊断治疗建议，不作为最终治疗方案，平台不对最终治疗方案负责；
            </p>
