            </p>
          </div>
          <div class="textCard">
            <p style="font-size: 14px;">
              9.	协诊前请仔细阅读本须知内容，发起协诊即视为同意本须知所述所有事项；
            </p>
          </div>
          <div class="textCard">
            <p style="font-size: 14px;">
              10.	如有疑问或投诉请致电我司客服电话进行咨询。
            </p>
