<script>
   import publicFn from '../../untils/Public.js';
   import defaultImg from '../../../static/img/default_avatar.png';
    export default {
        created:function(){
            // 獲取tab
          this.getList();
          this.Authorization=localStorage.getItem('access_token');
    //  獲取醫生列表
          var date = new Date();
          this.weekDateFn(date,"add")
        },
        name: 'ExpertList',
        data:function() {
            return{
              //
              page:{
                "pageNum" : 1,
                "pageSize" :15,
                "size" : null,
                "total" : null,
                "pages" : null,
                "prePage" : null,
                "nextPage" : null,
                "firstPage" : null,
                "lastPage" : null,
              },
               expertList:[],
                    "title":"专家在线",
                tabList:[
//                  {text:"全部",itemKey:"xxx",check:true},
//                  {text:"心血管",itemKey:"bbb",check:true},
//                  {text:"皮肤病",itemKey:"ccc",check:true},
//                  {text:"冠心病",itemKey:"aaa",check:true},
//                  {text:"心里生电",itemKey:"ddd",check:true},
//                  {text:"高血压",itemKey:"sss",check:true},
                ],
                Authorization:'',
                docterList:[],
                pagingBtn:true,
                index:0,
                total:0,
              //协整类型
              orderType:{textMage:{

              },
                vAndA:{ }
              },
              //专家小组
              expertGroup:null,
              //搜索框
              search:null,
              //本周时间
              weekData:null,
              //
              dat:null,
              //开始时间
              startDate:null,
              //结束时间
              endDate:null,
              //页面大小
              pageSize:10,
              //当前页
              pageNumber:1,
              //最大页
              maxPages:0,
              //出发日期悬浮
              fixedOff:false
            }
        },
         methods: {
          code:function () {

          },
// 预约医生
          goReservation:function (list,data){
             if(data.flag!=1){
               return
             }
             var parm=[];
             for( let i in list.orderType){
               parm.push({type: list.orderType[i].type,orderTypeId:list.orderType[i].id,price:list.orderType[i].price,status:1})
             }
             parm=JSON.stringify(parm);
             publicFn.publicLo.sessionStorageSav("Oder",parm);
             this.$router.push({ name: "Reservation",query: { id: list.usersId,date:data.date,type:list.expertGroup,patientId:this.$route.query.id}});
           },
           // tab列表
          getList:function(){
              var url='/api/basic/dictionary?search='+"疾病类型";
              this.Authorization=localStorage.getItem('access_token');
              this.$http.get(url).then(function(res){
                var parm=[];
                for(var i in res.body){
                  parm.push({text:res.body[i].name,itemKey:res.body[i].id,check:false})
                }
                parm.unshift({text:"全部",itemKey:"expect_group",check:true});
                this.numberCunt(parm);
              });
          },
           numberCunt(data){
            var parme={
              pageNumber:1,
              pageSize:999,
              type:2
            };
             if(this.search){
               parme.search = this.search
             }
             var url='/api/organization/employees/indexcount';
             this.$http.get(url, {
                       headers:{
                       Authorization: 'bearer '+ this.Authorization,
                      },
                      params:parme
                    }).then(function(res){
               for(var i in res.body){
                     data[i].number = res.body[i]
                 }
               this.tabList=data;
             });
           },
         // table查询
          getExperts:function(itemKey){
            if(itemKey){
              this.page.pageNum=1
            }
            for( var i in this.tabList){
              if(itemKey == this.tabList[i].itemKey){
                this.tabList[i].check=true;
                this.expertGroup=this.tabList[i].text
              }else{
                this.tabList[i].check=false;
              }
            }
            this.list()
          },
           //醫生詳細列表
         list(){
            //清空数据列表
             this.docterList=[];
             var parme ={
               pageNumber:this.page.pageNum,
               pageSize:this.page.pageSize,
               type:2,
             };
             if( this.expertGroup == "全部" || !this.expertGroup ){

             }else{
               parme.expertGroup=this.expertGroup
             }
             if(this.search){
               parme.search = this.search
             }
             var url='/api/organization/employees/index';
             this.$http.get(url,{
               headers:{
                 Authorization: 'bearer '+ this.Authorization,
               },
               params:parme
             }).then(function(res){
               res=res.body;
               var pages=res;
               this.page={
                 "pageNum" : pages.pageNum,
                 "pageSize" :pages.pageSize,
                 "size" : pages.size,
                 "total" : pages.total,
                 "pages" : pages.pages,
                 "prePage" : pages.prePage,
                 "nextPage" : pages.nextPage,
                 "firstPage" : pages.firstPage,
                 "lastPage" : pages.lastPage,
               }
               this.maxPages=res.pages;
               for( let i in res.list){
                 //前端记录专家顺序，解决排班回调数据混乱bug
                 res.upID=i;
                 //查看有头像img
                 if(!res.list[i].imageUrl){
                   //默认头像
                   res.list[i].imageUrl=defaultImg
                 }
                 //解析专家小组
                 if( res.list[i].expertGroup){
                   //专家小组数据切割
                   var expertGrouplist = res.list[i].expertGroup.split(",");
                   res.list[i].expertGroup=expertGrouplist;
                 }
                 this.work(res.list[i].usersId,res.list[i],i);
               }
             }).catch(function(res){
               this.warning("数据加载异常")
             })
           },
         searchFn(){
           this.page.pageNum=1;
           this.list();
           var data=this.tabList;
           this.numberCunt(data);
         },
           //首页
           homePage(){
             this.page.pageNum=1;
             this.list();
           },
           //尾页
           lastPage(){
             this.page.pageNum= this.page.pages;
             this.list();
           },
           //上一页
           previousPage(){
             if(this.page.pageNum-1<1){
               this.warning("第一页")
             } else {
               this.page.pageNum=this.page.prePage;
               this.list();
             }
           },
           //下一页
           nextPage(){
             if(this.page.lastPage<this.page.pageNum+1){
               this.warning("最后一页")
             } else {
               this.page.pageNum=this.page.nextPage;
               this.list();
             }
           },
          // 一周时间
          weekDateFn:function(date,parm){
          var dat =  publicFn.publicLo.weekDate(date,parm);
          var nwe =  publicFn.publicLo.dataChange();
          nwe=publicFn.publicLo.dataChange(nwe.today);
          var lat =  publicFn.publicLo.dataChange(dat[0].ymd);
            var go=false;
           if(nwe.time>lat.time){
             return
           }
           if(nwe.time==lat.time){
             var date =new Date();
             this.startDate= publicFn.publicLo.timeCheng(date);
             this.startDate=this.startDate.times;
             go=true;
           }

            this.weekData=dat;
            for(let i in dat){
              if(i==0){
                if(!go){
                  this.startDate=dat[0].ymd
                }
              }
              if(i==6){
                this.endDate=dat[6].ymd
              }
            }
            this.getExperts('expect_group');
          },
          //上下周
           nexWeek:function(parm){
             if(parm=="add"){
               this.weekDateFn(this.endDate,parm);

             }
             if(parm=="del"){
               this.weekDateFn(this.startDate,parm);
             }
           },
         //查询专家可约
           work:function(id,list){
             var dat=null;
             var url='/api/organization/assistSchedules';
             var token=localStorage.getItem('access_token');
             var parme={
               employeeId:id,
               startDate:this.startDate,
               endDate:this.endDate
             };
             this.$http.get(url,{
               headers:{
                 Authorization: 'bearer '+ token,
               },
               params:parme
             }).then(function(res){
               res=res.body;
               //初始化可约状态，全为无排班 0：满约，1：可约，3：无排班
               var dat=[{flag:3},{flag:3},{flag:3},{flag:3},{flag:3},{flag:3},{flag:3}];
               var parm=this.weekData;
               //获取当前时间
               var newTime=publicFn.publicLo.dataChange();
                 for(let i in parm){
                   //是否存在排班 0：不存在，1：存在
                   var falg=0;
                   if(res.length>0){
                     for(let k in res){
                       var time=publicFn.publicLo.timeCheng(res[k].date);//排班日期
                       if(time.ymd==parm[i].ymd){//匹配当本周日期和排班日期
                          if(res[k].flag==1){ //记录存在的可约排班
                            dat[i].flag=res[k].flag;
                            falg = 1;//存在排班
                          }
                          if(res[k].flag==0 && falg==0){
                            dat[i].flag=0
                          }
                           dat[i].date=time.ymd;
                       }
                     }
                   }
                 }
               //拼接排班数据
               list.dat=dat;
               //组装列表
               this.docterList.push(list);
               //还原专家列表原来排序
               if(this.docterList.length>=2){
                 publicFn.publicLo.sortData(this.docterList,"upID",false)
               }
             });
          },
           open:function() {
             this.$message('这是一条消息提示');
           },
           success:function() {
             this.$message({
               message: '恭喜你，这是一条成功消息',
               type: 'success'
             });
           },
           warning:function(text){
             this.$message({
               message: text,
               type: 'warning'
             });
           },
           erro:function() {
             this.$message.error('错了哦，这是一条错误消息');
           },
           //检测滚动条
           sollTopFn(){
             var that=this;
             document.onscroll = function(){
               if(document.body.scrollTop>=180){
                   that.fixedOff=true;
               }else {
                 that.fixedOff=false;
               }
             }
           }
      },
      mounted:function() {
              this.sollTopFn();
      },
    }
</script>



